.linktree {
  text-align: center;
  background-color: black;  }

  @media only screen and (max-width: 600px) {
    .link-button {
      font-size: 0.8em;
      padding: 0.3em 0.7em;
    }
  }
  
  
  .link-button {
    display: block;
    border: 1px solid white;
    border-radius: 4px;
    padding: 0.5em 1em;
    margin: 0.5em;
    background-color: black;
    color: white;
    font-family: 'Montserrat';
  }
  
  
  .social-logo {
    width: 1em;
    height: 1em;
    margin-right: 0.5em;
  }
  